html {
  box-sizing: border-box;
  font-family: "lato", sans-serif;
}

*, ::after, ::before {
  box-sizing: inherit;
}

body {
  background-color: #F0F0F0;
  align-content: center;
  padding: 0px 40px;
  max-width: 1000px;
  margin: 0 auto;
  font-family: 'Lato' sans-serif;
  word-break: break-word;
}
.form {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
    margin: 0 auto;
    background-color: white;
}

.form__field {
    border: 2px solid #F0F0F0;
    padding: 8px;
    margin: 0;
}

.form__button {
    background-color: teal;
    border: none;
    padding: 12px;
    color: white;
    transition: 0.5s;
}

.form__button:hover {
    background-color: hsl(180, 60%, 25%);
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    cursor: pointer;
}

@media (max-width: 767px) {
    .form {
        grid-template-columns: 1fr;
    }
}
.list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.list__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 10px;
    align-items: center;
    padding: 10px ;
    border-bottom: 1px solid #ccc;
}

.list__item--hidden {
    display:none;
}

.list__itemContent--done {
    text-decoration: line-through;
}

.list__button {
    background-color: hsl(122, 39%, 49%);
    color: white;
    border: none;
    width: 30px;
    height: 30px;
    padding: 0;
    cursor: pointer;
    transition: 1s;
}

.list__button:hover {
    background-color: hsl(122, 39%, 25%);
}

.list__button--done:hover {
    background-color: hsl(122, 39%, 25%);
}

.list__button--remove {
    background-color: #FF405B;
    color: white;
    border: none;
    width: 30px;
    height: 30px;
    padding: 0;
    cursor: pointer;
    transition: 1s;
}

.list__button--remove:hover {
    background-color: hsl(352, 100%, 35%);
}
.buttons {
    padding-right: 20px;
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 10px;
}

.buttons__button {
    border: none;
    background-color: white;
    color: teal;
    transition: 0.5s;
}

.buttons__button:hover {
    color: hsl(180, 60%, 20%);
    cursor: pointer;
}

.buttons__button:disabled {
    color: #ccc;
    cursor: auto;
}

@media (max-width: 767px) {
    .buttons {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 10px;
        align-items: center;
        padding-right: 0;
        padding-bottom: 20px;
    }
}
.section {
    background-color: white;
    max-width: 1000px;
    margin: 10px 0;
}

.section__title {
    padding: 20px;
    margin: 0;
    font-size: 18px;
    font-weight: 700;
}

.section__body {
    padding: 20px;
}

.section__header {
    display: grid;
    grid-template-columns: 1fr auto;
    margin: 0 auto;
    align-items: center;
    border-bottom: 2px solid #F0F0F0;
}

@media (max-width: 767px) {
    .section__header {
        grid-template-columns: 1fr;
    }
}
